/* App.css */

/* Reset default margins and paddings */
body,
h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
}

/* Set background color for the whole page */
body {
  background-color: #f8f8f8;
}

/* Center the content vertically and horizontally */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-family: 'Roboto', sans-serif;
}

/* Style the header section */
.App-header {
  text-align: center;
  padding: 2rem;
  background-color: #ffffff;
}

.App-header h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #333333;
}

.App-header p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  color: #666666;
}

.App-header .App-logo {
  width: 100px;
  height: 100px;
  margin-bottom: 1.5rem;
}

.App-header a {
  color: #61dafb;
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}

.App-header a:hover {
  color: #0077b6;
}

/* Style the About section */
.About {
  padding: 2rem;
  background-color: #ffffff;
}

.About h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #333333;
}

.About p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  color: #666666;
}

/* Style the Projects section */
.Projects {
  padding: 2rem;
  background-color: #f1f1f1;
}

.Projects h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #333333;
}

.Project {
  margin-bottom: 2rem;
}

.Project h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: #333333;
}

.Project p {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  color: #666666;
}

/* Style the Contact section */
.Contact {
  padding: 2rem;
  background-color: #ffffff;
}

.Contact h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #333333;
}

.Contact p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  color: #666666;
}

/* Style the Footer */
.App-footer {
  background-color: #f1f1f1;
  padding: 1rem;
  text-align: center;
  font-size: 1rem;
  color: #666666;
}

/* Media Query for smaller screens */
@media only screen and (max-width: 768px) {
  .App-header h1 {
    font-size: 2rem;
  }
  
  .About h2,
  .Projects h2,
  .Contact h2 {
    font-size: 1.8rem;
  }
}
